import { useRef } from 'react';
import axios from 'axios';
import { LinkDefaultStyle } from './style/MainPage.style';
import { AccountContainer, AccountForm } from './style/Login.style';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

export default function SignUp() {
    let emailRef: any = useRef();
    let nameRef: any = useRef();
    let passwordRef: any = useRef();
    const navigate = useNavigate();

    function handleSignUpForm(event: any) {
        event.preventDefault();

        //  input    
        let emailValue = emailRef.current.value;
        let nameValue = nameRef.current.value;
        let passwordValue = passwordRef.current.value;

        //   
        const emailCheck =
            /^[-!#$%&'*+./0-9=?A-Z^_a-z{|}~]+@[-!#$%&'*+/0-9=?A-Z^_a-z{|}~]+.[-!#$%&'*+./0-9=?A-Z^_a-z{|}~]+$/;

        // input     
        if (emailValue && nameValue && passwordValue) {
            //     
            if (emailCheck.test(emailValue)) {
                axios
                    .post('http://localhost:5000/api/users/register', {
                        email: emailValue,
                        name: nameValue,
                        password: passwordValue,
                    })
                    .then((res) => {
                        Swal.fire({
                            icon: 'success',
                            title: 'Success',
                            text: '  !',
                        });
                        console.log(res);
                        navigate('/shoeshop/login');
                    })
                    .catch((error) => {
                        console.log(`Login Error : ${error}`);
                    });
            } else {
                Swal.fire({
                    icon: 'error',
                    title: 'Error',
                    text: '  . or  .',
                });
            }
        } else {
            Swal.fire({
                icon: 'error',
                title: 'Error',
                text: '   .',
            });
        }
    }

    return (
        <AccountContainer>
            <h2>Create Account</h2>
            <AccountForm>
                <input type="email" placeholder="email" ref={emailRef} />
                <input type="text" placeholder="name" ref={nameRef} />
                <input type="password" placeholder="password" ref={passwordRef} />
                <button onClick={handleSignUpForm}></button>
                <div>
                    <span>{`  ?`} </span>
                    <LinkDefaultStyle to="/portfolio_shoppingmall_frontend/login"></LinkDefaultStyle>
                </div>
            </AccountForm>
        </AccountContainer>
    );
}
