import { useRef } from 'react';
import { LinkDefaultStyle } from './style/MainPage.style';
import { AccountContainer, AccountForm } from './style/Login.style';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

export default function Login() {
    let emailRef: any = useRef();
    let passwordRef: any = useRef();
    const navigate = useNavigate();

    function handleLoginForm(event: any) {
        event.preventDefault();

        //  input   
        let emailValue = emailRef.current.value;
        let passwordValue = passwordRef.current.value;

        // ,     input   
        if (!emailValue || !passwordValue) {
            alert('  .');
        } else {
            axios
                .post('http://localhost:5000/api/users/login', {
                    email: emailValue,
                    password: passwordValue,
                })
                .then((res) => {
                    // console.log(res.data.token);
                    console.log(res.data);
                    navigate('/shoeshop/');
                })
                .catch((error) => {
                    console.log(`Login Error : ${error}`);
                });
        }
    }

    return (
        <>
            <AccountContainer>
                <h2>Login Account</h2>
                <AccountForm>
                    <input type="text" placeholder="email" ref={emailRef} />
                    <input type="password" placeholder="password" ref={passwordRef} />
                    <button onClick={handleLoginForm}></button>
                </AccountForm>
                <footer>
                    <span>{`  ?`} </span>
                    <LinkDefaultStyle to="/portfolio_shoppingmall_frontend/signup"></LinkDefaultStyle>
                </footer>
            </AccountContainer>
        </>
    );
}
